html, body {
    width: 100%;
    height: 100%;
    -webkit-overflow-scrolling: auto;
    font-family: Graphik, Helvetica, sans-serif;
    text-rendering: optimizeLegibility;
    -webkit-font-feature-settings: 'kern';
    -ms-font-feature-settings: 'kern' 1;
    font-feature-settings: 'kern';
    -webkit-font-smoothing: antialiased;

}

@font-face {
    font-family: Graphik;
    src: url('https://pik.ru.cdn.pik-service.ru/site/fonts/GraphikLCGMedium.eot');
    src: url('https://pik.ru.cdn.pik-service.ru/site/fonts/GraphikLCGMedium.woff') format('woff'),
    url('https://pik.ru.cdn.pik-service.ru/site/fonts/GraphikLCGMedium.ttf') format('truetype'),
    url('https://pik.ru.cdn.pik-service.ru/site/fonts/GraphikLCGMedium.svg') format('svg');
    font-weight: 500
}

body {
    background-color: #F8F8F8;
    font-size: 14px;
    color: #000;
    margin: 0px;
    overflow: hidden;
}

.player {
    height: 100%;
}

.progress {
    position: absolute;
    top: calc(50% - 3px);
    left: 50%;
    transform: translateX(-50%);
    max-width: 512px;
    width: calc(100% - 64px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    border-radius: 3px;
    background: rgba(149, 155, 166, 0.12);
}

.progress__bar {
    background-color: #f63;
    height: 6px;
    border-radius: 3px;
    transition: width .2s ease-in;
}

.stats {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    position: absolute;
    pointer-events: none;
    color: rgb(0, 172, 72);
    font-size: 16pt;
}
